import React from "react";
import {FontAwesomeIcon as Fa} from "@fortawesome/react-fontawesome";
import {faArrowUp} from "@fortawesome/free-solid-svg-icons";
import {fetch} from "whatwg-fetch";

const UpvoteComponent = ({slug, upvoteCount, setArticleData}) => {

    const upvoteArticle = async () => {
        const result = await fetch(`/api/articles/upvote/${slug}`, {
            method: 'POST'
        });
        const body = await result.json();
        setArticleData(body.article);
    }

    return (
        <div className="py-3">
            <button className="btn btn-sm btn-info me-2" onClick={()=> upvoteArticle()}><Fa icon={faArrowUp} /> Upvote</button>
            This post have {upvoteCount} { upvoteCount > 1 ? 'upvotes' : 'upvote' }.
        </div>
    );
}

export default UpvoteComponent;
