import React from "react";
import {FontAwesomeIcon as Fa} from "@fortawesome/react-fontawesome";
import {faClock} from "@fortawesome/free-solid-svg-icons";
import moment from "moment";

const CommentsComponent = ({comments}) => {
    return (
        <>
            {
                comments.length ? comments.map((comment, key) => (
                        <div className="d-flex mb-2" key={key}>
                            <div className="flex-shrink-0">
                                <img src="/user.jpg" alt="user" style={{width: 64}} />
                            </div>
                            <div className="flex-grow-1 ms-3">
                                <span className="fw-bold text-capitalize">{comment.name}</span>
                                <small className="d-block"><Fa icon={faClock} /> {moment(comment.created_at).fromNow()}</small>
                                <p>{comment.comment}</p>
                            </div>
                        </div>
                    )
                ) : `No comment found`
            }
        </>
    )
};

export default CommentsComponent;
