import React, {useEffect, useState} from "react";
import parse from 'html-react-parser';
import NotFoundPage from "./NotFoundPage";

import UpvoteComponent from "../components/UpvoteComponent";
import CommentsComponent from "../components/CommentsComponent";
import CommentFormComponent from "../components/CommentFormComponent";

const ArticlePage = ({ match }) => {
    const name = match.params.name;
    const [article, setArticleData] = useState({title: '', content: ''});
    const [comments, setComments] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            setArticleData(body.article);
            setComments(body.comments);
        }
        fetchData();
    }, [name]);

    if (! article) {
        return <NotFoundPage />
    }

    return (
        <>
            <h1>{ article.title }</h1>
            <UpvoteComponent slug={name} upvoteCount={article.votes} setArticleData={setArticleData} />
            { parse(article.content) }
            <hr className="my-4" />
            <h3>Comments</h3>
            <CommentsComponent comments={comments} />
            <hr className="my-4" />
            <CommentFormComponent slug={name} setComments={setComments} />
        </>
    );
}

export default ArticlePage;
