import React, {useState} from "react";

const CommentFormComponent = ({slug, setComments}) => {
    const [fullName, setFullName] = useState('');
    const [comment, setComment] = useState('');

    const addComment = async () => {
        const result = await fetch(`/api/articles/add-comment/${slug}`, {
            method: 'post',
            body: JSON.stringify({
                fullname: fullName,
                comment: comment
            }),
            headers: {
                'Content-Type': 'application/json'
            }
        });
        const data = await result.json();
        setComments(data.comments);
        setFullName('');
        setComment('');
    };

    return (
        <div className="my-3">
            <h4>Add Comment</h4>
            <div className="mb-3">
                <label htmlFor="exampleFormControlInput1" className="form-label">Full Name</label>
                <input type="email" className="form-control" id="exampleFormControlInput1" value={fullName} onChange={e => setFullName(e.target.value)} />
            </div>
            <div className="mb-3">
                <label htmlFor="exampleFormControlTextarea1" className="form-label">Comment</label>
                <textarea className="form-control" id="exampleFormControlTextarea1" rows="3" value={comment} onChange={e => setComment(e.target.value)}></textarea>
            </div>
            <button className="btn btn-primary mb-3" onClick={() => addComment()}>Submit</button>
        </div>
    );
};

export default CommentFormComponent;
